.create-site-form {
    width: 70vw;
    height: 70vh;
    background-color: white;
    color: black;
    font-family: 'PT Sans', sans-serif;
    overflow: auto;
}

.image-upload-error-container {
    display: flex;
    gap: 5px;
    flex-direction: column;
}

.image-upload-error-container p {
    color: red;
}

.create-site-form h2 {
    color: black;
    padding-top: 15px;
}

.form-info {
    display: inline-grid;
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    padding: 20px;
    display: flex;
    flex-direction: column;
    width: 90%;
    height: 80%;
}

.form-info input {
    padding: 10px 5px;
    border: 1px solid rgb(0, 0, 0, 0.5);
    width: 80%;
}

.upload-images {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 10px;
}


.upload-images input[type="file"] {
    display: none;
}

.upload-images label {
    border: 1px solid black;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    text-align: center;
    border-radius: 20px;

    &.max-images {
        background: rgb(205, 203, 203);
        pointer-events: none;
    }
}

.create-site {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 4fr 1fr;
    width: 100%;
    max-height: 80%;
    padding: 20px;
}


#newsite {
    display: flex;
    justify-content: space-between;
    margin: 5px;
    align-items: center;
}


#create-site-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-row: 2 / 3;
    grid-column: 1 / 3;
    padding: 10px 0;
}

#create-site-buttons button {
    margin: 20px;
    width: 150px;
}

.edit-sites {
    position: relative;
    top: 200px;
}

.photo-preview-container {
    border: 1px solid black;
    height: 80%;
    max-height: 320px;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;
}

.photo-preview-container i {
    background-color: grey;
    color: white;
    padding: 5px 10px;
    border-radius: 50%;
    font-size: 20px;
    position: absolute;
    margin: 5px;
}

.photo-preview-container i:hover {
    opacity: 0.5;
}